#landing {
    /* background: linear-gradient(rgb(16, 106, 196), rgb(81, 161, 241), rgb(132, 186, 240)); */
    /* overflow-y:scroll; */
    text-align:center;
    margin: 25vh auto;
}
.zoomInRight {
    -webkit-animation-delay:.5s;
            animation-delay:.5s;
}

.zoomInLeft {
    -webkit-animation-delay:1s;
            animation-delay:1s;
}

.arrow {
    -webkit-animation-delay:1.5s;
            animation-delay:1.5s;
    font-size:45px;
}
.bounce{
    /* animation-duration:1s; */
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

h1{
    font-size:45px;
}

@media (max-width:320px) {
    h1 {
        font-size:35px;
    }
}


